const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			indicatorDots: true,
			Inv: 0,
			list: [],
			keywords: '',
			navList: [],
			tabType: 'is_new',
			page_size: '10',
			page:'1',
			imgUrl: ''
		};
	},
	onLoad() {
		this.getNav();
		this.getTab();
		this.imgUrl = helper.imgUrl;
	},
	methods: {
		navsHead() {
			uni.navigateTo({
				url:'/pages/allProduct'
			})
		},
		changeTab(e){
			console.log(e)
			this.Inv = e;
			this.page = '1'
			if(this.Inv == 0){
				// 最新
				this.tabType = 'is_new'
			} else if(this.Inv == 1){
				// 热门
				this.tabType = 'is_hot'
			} else if(this.Inv == 3){
				// 推荐
				this.tabType = 'is_romend'
			}
			this.list = []
			this.getTab()
		},
		getSearch() {
			uni.navigateTo({
				url:'/pages/searchNetloan'
			})
		},
		//导航
		getNav() {
			uni.request({
				url: `${helper.requestUrl}/holes/categories`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						let arr = res.data.data;
						let result = [];
						let num = 10
						for (let i = 0; i < arr.length; i += num) {
							result.push(arr.slice(i,i+num))
						}
						console.log(result)
						this.navList = result
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		// 最新
		getTab() {
			uni.request({
				url: `${helper.requestUrl}/holes/index-recommends`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data:{
					type: this.tabType,
					page_size:this.page_size,
					page:this.page
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						this.list = this.list.concat(res.data.data)
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		goProduct(e){
			console.log(e)
			let id = e.currentTarget.dataset.id
			uni.navigateTo({
				url:`/pages/productDetail?id=${id}`
			})
		},
		onReachBottom() {
			this.page ++;
			this.getTab()
		}
	}
};
